.greetingForm {
    height: 240px
}

.inputAndButtonContainer {
    display: flex;
    gap: 12px;
}

.error {
    margin-top: 5px;
    font-weight: 400;
    font-size: 14px;
    color: #c00;
}

.input {
    width: 372px;
    height: 36px;
    padding: 8px 12px;
    border: 1px solid #ccc;
    border-radius: 4px;

}

.input:focus {
    width: 372px;
    height: 36px;
    padding: 8px 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.errorInput {
    width: 372px;
    height: 36px;
    outline: none;
    padding: 8px 12px;
    border: 1px solid #c00;
    border-radius: 4px;

}

.button {
    border-radius: 3px;
    padding: 5px 24px;
    width: 78px;
    height: 36px;
    background: #06c;
    font-weight: 600;
    font-size: 14px;
    line-height: 143%;
    color: #fff;
    outline: none;
}

.button:focus {
    border-radius: 3px;
    padding: 5px 24px;
    width: 78px;
    height: 36px;
    background: #06c;
    font-weight: 600;
    font-size: 14px;
    line-height: 143%;
    color: #fff;
    outline: none;
}

.button:active {
    border-radius: 3px;
    padding: 5px 24px;
    width: 78px;
    height: 36px;
    background: #06c;
    font-weight: 600;
    font-size: 14px;
    line-height: 143%;
    color: #fff;
    outline: none;}

.button:disabled {
    background: #06c;
    opacity: 0.5;
}

.text {
    font-weight: 400;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.5);
    margin-bottom: 10px;
}

